.products .card-image {
  width: 100%;
}

.card {
  width: 100%;
  max-width: 300px;
  margin: 0 0 1.5rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.card-ratio {
  padding-bottom: 18%;
}

.card-name {
  color: #FFFFFF;
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  z-index: 10;
  margin: 0;
}

.card-curve {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
}

