.header-full-screen {
  height: 100vh;
  width: 100%;
}

.header-small {
  min-height: 48px;
  width: 100vw;
  z-index: 9999;
  position: fixed;
  margin-bottom: 48px;
}

.header-mobile {
  min-height: 100vh;
  display: none;
}

.header-mobile-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  height: 40vh;
  translate: -50% -50%;
}

.header-mobile.collapse {
  min-height: 48px;
}

.header-small-strip, .header-mobile-strip {
  min-height: 48px;
}

.header-image {
  background-image: url("../assets/beads-bw.jpeg");
  background-size: cover;
  z-index: 5;
}

.header-color {
  height: 100vh;
  position: absolute;
  top: 0;
  background-size: cover;
  opacity: 0.7;
  z-index: 10;
}

.header-logo-container,
.header-mobile-logo-container {
  position: fixed;
  text-align: center;
  top: 0;
  width: 100vw;
  z-index: 9999;
}

.header-mobile-logo-container {
  height: 100vh;
  transition: 0.5s;
}

.header-logo-container,
.header-mobile.collapse > .header-mobile-logo-container {
  height: 48px;
}

.header-logo-image {
  position: absolute;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
}

.header-logo {
  z-index: 50;
  position: relative;
}

.logo-large {
  min-height: 24px;
  min-width: 211.250px;
  max-width: 75vw;
  z-index: 5;
  position: relative;
}

.logo-small,
.logo-mobile.collapse {
  height: 24px;
  margin-top: 0.5rem;
}

.logo-mobile {
  max-width: 75vw;
}

.header-mobile-strip {
  translate: 0 -100%;
  transition: 1s;
}

.header-mobile.collapse .header-mobile-strip {
  translate: 0;
}

@media screen and (max-width: 767px) {
  .header-large {
    display: none;
  }

  .header-mobile {
    display: block;
  }
}
