@keyframes marquee {
  0% {
    translate: 0%;
  }
  100% {
    translate: -100%;
  }
}

.marquee-animation {
  animation: marquee infinite 2s linear;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in-animation {
  animation: fade-in 0.5s linear;
  animation-delay: 0.5s;
}
