.designs .card-image {
  width: 100%;
}

.card {
  width: 100%;
  max-width: 300px;
  margin: 0 0 1.5rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.card-ratio {
  padding-bottom: 18%;
}

.card-name {
  color: #FFFFFF;
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  z-index: 10;
  margin: 0;
}

.card-curve {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
}

.about-container {
  line-height: 1.5;
  color: white;
  margin-bottom: 48px;
}

.about-right, .about-left {
  min-height: 40vmax;
  padding: 3rem 2rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-left {
  /* background-image: url('../../assets/bracelets.jpeg'); */
  background-image: url("../../assets/beads.jpeg");
  background-size: cover;
  position: relative;
}

.about-left-text-image {
  width: 85%;
  z-index: 10;
}

.about-left-mask {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 5;
  opacity: .8;
}

.about-right {
  text-align: justify;
}

.address {
  margin-bottom: 48px;
}